@import '../root.scss';

.root {
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background: $primary-bg-2;
  color: $light-1;
  border: 1px solid $light-2;
}

.img {
  border-radius: 50%;
  width: 100%;
  height: 100%;
  object-fit: contain;
}